$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

// Mixins
@mixin transition($transitions) {
     -moz-transition: $transitions;
       -o-transition: $transitions;
      -ms-transition: $transitions;
  -webkit-transition: $transitions;
          transition: $transitions;
}

.page-template-page-contact {
  #contactForm {
    background: #f2f2f2;
    .contactFormContainer {
      background: $white;
      border-top: 2px solid $yellow;
      margin-top: 40px;
      box-shadow: 0 0 25px -9px #333;
      transform: scale(1.01);
      h3 {
        &:first-child {
          margin-top: 0;
        }
      }
      hr {
        width: 100px;
        border-color: $yellow;
        margin-left: 0;
        border-width: 2px;
      }
      input[type="text"], input[type="email"], input[type="url"], input[type="tel"], textarea, select {
        width: 100%;
        padding: 10px;
        border-color: #f2f2f2;
        border-radius: 0;
        margin: 5px;
      }
      input[type="submit"] {
        padding: 10px 25px;
        border-radius: 25px;
        border: 2px solid $yellow;
        font-size: 0.8rem;
        transition: all 0.2s ease-in-out;
        line-height: 1.5;
        &:hover {
          cursor: pointer;
        }
      }
      &.inactive {
        transform: scale(1);
        box-shadow: none;
        border-top-color: #ddd;
        hr {
          border-color: #ddd;
        }
      }
    }
  }
  #researchPark {
    background: $blue;
    color: $white;
    padding-top: 0; padding-bottom: 0;
    min-height: 400px;
    h2 {
      color: $white;
    }
    .headingSpacer {
      background-color: $yellow;
    }
    .aboutIntroSliderContainer {
      height: 400px;
      .aboutIntroSlider {
        position: absolute;
        top: 0; right: 0; left: 0; bottom: 0;
        overflow: hidden;
        .aboutIntroSliderItem {
          height: 400px;
          background-position: center;
          background-size: cover;
        }
      }
    }
  }
}